.cardHolder{
    background: rgba(16, 16, 53, 0.488);
    border-radius: 2px;
    max-height: 185px;
    margin: 1rem;
    /* position: relative; */
    max-width: 185px;

}

img{
    width: 175px;
    height: 175px;
    display: flex;
    border-color: black;
    border: 5px;
    padding: 5px 5px 5px 5px;
    
}
.container {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.header {
    width: 100%;
    text-align: center;
    font-size: 2rem;
    text-decoration: underline;
    color: rgb(6, 34, 55);
    margin-bottom: 50px;
    font-family: Calibri, sans-serif;
}
